@use "../global/variables.scss";
@use "../global/mixins.scss";

.list-radio {
  @include mixins.list-reset;
  display: flex;
  flex-direction: column;
  gap: 7px 0;

  @media (min-width: variables.$width-tablet) {
    gap: 24px 0;
  }
}

.list-radio__input-radio {
  &:checked + .list-radio__mark-radio {
    &::after {
      position: absolute;
      content: "";
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background-color: variables.$moderate-aquamarine-2;
    }

    @media (min-width: variables.$width-tablet) {
      &::after {
        width: 20px;
        height: 20px;
      }
    }
  }
}

.list-radio__mark-radio {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  box-shadow: inset 0 0 0 2px variables.$dark-bluish-black;
  border-radius: 50%;

  &:hover {
    box-shadow: inset 0 0 0 2px variables.$moderate-aquamarine-3;
  }

  &:active {
    box-shadow: inset 0 0 0 2px rgba(variables.$dark-main, 0.3);
  }

  @media (min-width: variables.$width-tablet) {
    width: 50px;
    height: 50px;
  }
}
